@custom-media --xs (min-width: 480px);
@custom-media --xs-down (max-width: 479px);
@custom-media --sm (min-width: 640px);
@custom-media --sm-down (max-width: 639px);
@custom-media --md (min-width: 768px);
@custom-media --md-down (max-width: 767px);
@custom-media --lg (min-width: 1024px);
@custom-media --lg-down (max-width: 1023px);
@custom-media --xl (min-width: 1280px);
@custom-media --xl-down (max-width: 1279px);
@custom-media --2-xl (min-width: 1536px);
@custom-media --2-xl-down (max-width: 1535px);
.wp-block-columns:not([class^=are-vertically-aligned]) {
  align-items: initial;
}
.wp-block-columns:where(:not([class^=is-stacked-on])) {
  flex-direction: column;
}
.wp-block-columns.are-vertically-aligned-top {
  align-items: flex-start;
}
.wp-block-columns.are-vertically-aligned-center {
  align-items: center;
}
.wp-block-columns.are-vertically-aligned-bottom {
  align-items: flex-end;
}
.wp-block-columns:where(.is-reversed-when-stacked) {
  flex-direction: column-reverse;
}
@media (--sm) {
  .wp-block-columns:where(.is-stacked-on-sm), .wp-block-columns:where(:not([class*=is-stacked-on])) {
    flex-direction: row;
  }
}
@media (--md) {
  .wp-block-columns:where(.is-stacked-on-md) {
    flex-direction: row;
  }
}
@media (--lg) {
  .wp-block-columns:where(.is-stacked-on-lg) {
    flex-direction: row;
  }
}
@media (--xl) {
  .wp-block-columns:where(.is-stacked-on-xl) {
    flex-direction: row;
  }
}